import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import TestPackageImg from "../assets/images/support-learner.svg";
import TestPackageImg2 from "../assets/images/support-instructor.svg";
import { apiGet } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";

const Support = () => {
  const [supportType, setSupportType]=useState('learner')
  const [menuItems, setMenuItems]=useState([])
  
  // const menuItems = [
  //   {
  //     title: "General information",
  //     subtitles: ["Lorem ipsum dolor sit amet 1.1", "Figma ipsum component 1.2", "Figma ipsum component variant 1.3",],
  //   },
  //   {
  //     title: "Manage your Bookings",
  //     subtitles: ["Figma ipsum component 2.2", "Another subtitle 2.3"],
  //   },
  //   {
  //     title: "Manage Your Account",
  //     subtitles: ["Account subtitle 3.1", "Account subtitle 3.2"],
  //   },
  //   {
  //     title: "Manage Your Payments",
  //     subtitles: ["Payments subtitle 4.1", "Payments subtitle 4.2"],
  //   },
  // ];

  // const content = {
  //   "Lorem ipsum dolor sit amet 1.1":
  //     "<h4>Figma ipsum component variant main layer.</h4> Description for Lorem ipsum dolor sit amet <li>abc</li>",
  //   "Figma ipsum component 1.2": "Description for Figma ipsum component",
  //   "Figma ipsum component variant 1.3":
  //     "Description for Figma ipsum component variant",
  //   "Account subtitle 2.2": "Description for Account subtitle 2",
  //   "Payments subtitle 3.1": "Description for Payments subtitle 1",
  //   "Payments subtitle 3.2": "Description for Payments subtitle 2",
  //   "Another subtitle": "Description for Another subtitle",
  // };

    // Set initial state to the first title and first subtitle
   // const [activeAccordion, setActiveAccordion] = useState(0); // Set the first accordion active by default
   // const [selectedSubtitle, setSelectedSubtitle] = useState(      menuItems[0].subtitles[0]    ); // Default to first subtitle
   const [activeAccordion, setActiveAccordion] = useState(0); // Set the first accordion active by default
   const [selectedSubtitle, setSelectedSubtitle] = useState([]); // Default to first subtitle
  const fetchLearnerSupport=async ()=>{
    try{
      const learnerSupportResponse = await apiGet(`${API_ENDPOINTS.SUPPORT_INFO}?supportType=${supportType}`);
     // console.log(learnerSupportResponse.data)
      if(learnerSupportResponse.success){
        setMenuItems(learnerSupportResponse.data)
         setSelectedSubtitle(learnerSupportResponse.data[0].support_posts[0].description)
      }
    }
    catch(error){
        // console.log(error);
    }
    
  }

  useEffect(()=>{
    fetchLearnerSupport()
  },[supportType])

  // Function to toggle which accordion is open
  const toggleAccordion = (index) => {
    setActiveAccordion(activeAccordion === index ? null : index);
  };

  // Handle subtitle click to update content
  const handleSubtitleClick = (subtitle) => {
    setSelectedSubtitle(subtitle);
    //console.log(subtitle)
  };

  const changeSupport=(item)=>{
    setSupportType(item)
    //console.log(item)
  }

  return (
    <>
      <div className="Support__TestPackageCards text-center">
        <Container>
          <Row>
            <Col lg={12}>
              <h1>Support</h1>
              <div className="TestPackage__wrp">
                <div className={`TestPackage__card ${supportType === 'learner' ? 'active' : ''}`} onClick={()=>{changeSupport('learner')}}>
                  <img src={TestPackageImg} alt="TestPackage__card img" />
                  <h3>Learners</h3>
                  <p>Driving test package with driving lessons</p>
                </div>
                <div className={`TestPackage__card ${supportType === 'instructor' ? 'active' : ''}`} onClick={()=>{changeSupport('instructor')}}>
                  <img src={TestPackageImg2} alt="TestPackage__card img"/>
                  <h3>Instructors</h3>
                  <p>Stand alonedriving test package</p>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <Container>
        <Row>
          <Col lg={12}>
            <div className="Support">
              {/* Left Accordion Menu */}
              <div className="Support__left">
                <div className="accordionMenu">
                  {menuItems?.map((item, index) => (
                    <div key={index} className="accordionMenu__item">
                      <div
                        className={`accordionMenu__title ${
                          activeAccordion === index ? "active" : ""
                        }`}
                        onClick={() => toggleAccordion(index)}
                      >
                        {item.title}
                      </div>

                      {activeAccordion === index && (
                        <div className="accordionMenu__subtitles">
                          {item.support_posts.map((subtitle, subIndex) => (
                            <div
                              key={subIndex}
                              className={`accordionMenu__subtitle ${
                                selectedSubtitle === subtitle ? "active" : ""
                              }`}
                              onClick={() => handleSubtitleClick(subtitle.description)}
                            >
                              {subtitle.title}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>

              {/* Right Content Area */}
              <div className="Support__right">
                <div className="contentArea">
                  {/* <h2>{selectedSubtitle}</h2> */}
                  <div
                    dangerouslySetInnerHTML={{
                      __html:
                      selectedSubtitle
                        // content[selectedSubtitle] ||
                        // "Select a subtitle to see the content.",
                    }}
                  />
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Support;
